 * @Description: In User Settings Edit
 * @Author: your name
 * @Date: 2019-08-27 15:31:38
 * @LastEditTime: 2019-08-27 16:28:49
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div class="search_wrapper">
    <Header></Header>
    <div class="search_content">
        <bread-crumb :crumbs="crumbs"></bread-crumb>

        <el-row class="search">
          <el-col class="coin_trade_title" >
              <p class="search_result">搜索结果</p>
              <p class="detail"><span>杠杆</span>的结果：12条</p>
          </el-col>
          <el-col class="coin_trade_content" @click.native="goTo('币币交易')">阶梯手续费率和HT抵扣手续费-操作指南（网页端）</el-col>
          <el-col class="coin_trade_content">关于BTC合约实时结算功能上线的公告 </el-col>
          <el-col class="coin_trade_content">阶梯手续费率和HT抵扣手续费-操作指南（网页端）</el-col>
          <el-col class="coin_trade_content">关于BTC合约实时结算功能上线的公告</el-col>
        </el-row>
    </div>
  </div>
</template>

<script>
    import Header from '@/components/header/header'
    import BreadCrumb from '@/components/breadcrumb/breadcrumb'

    export default {
    components: {Header, BreadCrumb},
    props: {},
    data() {
        return {
            crumbs:[]
        }
    },
    watch: {},
    methods: {
        /* 解析路由地址 */
        analyAddress(){
            const route = this.$route
            const bread = JSON.parse(localStorage.getItem('breads'))
            
            if(route.fullPath.indexOf('?') != -1){
                var cbs = [{name:route.query.name,url:route.fullPath}]
                cbs.unshift(bread)
                this.crumbs = cbs
            }
        },
        /* 公告详情 */
        goTo(){
            const meta = {name:'帮助中心', url:'/noticeList'}
            localStorage.setItem('breads',JSON.stringify(meta))
            this.$router.push({path:'/noticeDetail',query:{name:'详情'}})

        }
    },
    created() {
        this.analyAddress()
    },
    mounted() {}
    };
</script>
<style lang="scss" scoped>
    @import '@/style/base.scss';
    .search_wrapper{
        width: 100%;
        min-height: 100%;
        background-color: $light_font_color;
        min-width: 1280px;
        padding-top: 44px;
        padding-bottom: 15px;
        box-sizing: border-box;
        .search_content{
            width: 1200px;
            margin: 0 auto;
            padding: 15px 0 60px 0px; 
            box-sizing: border-box;
            background-color:$light_font_color;
            .search{
                margin-top: 50px;
                padding-left:100px; 
                box-sizing: border-box;
            }
            .coin_trade_title{
                color: $noraml_font_color;
                border-bottom: 1px solid $border_dark_color;
                .search_result{
                    font-size: $font20;
                    font-weight: 600;
                    line-height: 30px;
                }
                .detail{
                    font-size: $font16;
                    line-height: 40px;
                }
            }
            .coin_trade_content{
                line-height: 60px;
                font-size: $font18;
                color: $dark_title_color;
                border-bottom: 1px solid $border_dark_color;
                cursor: pointer;
                &:hover{
                    text-decoration:underline;
                }
            }
        }
    }
</style>


